<template>
    <el-dialog :title="titleShow==true?'add':'edit'"  v-model="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="CompanyCode:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.CompanyCode" auto-complete="off" size="small"></el-input>
            </el-form-item>
            <el-form-item label="Amount:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Amount" auto-complete="off" size="medium"></el-input>
            </el-form-item>
            <el-form-item label="EffectiveDate:" :label-width="formLabelWidth">
                <el-date-picker
                        size="small"
                        v-model.trim="singleDate"
                        type="date"
                        placeholder="Select EffectiveDate">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="EmpCode:" :label-width="formLabelWidth">
                <el-input v-model.trim="EmpCode" auto-complete="off" disabled></el-input>
            </el-form-item>
            <!--<el-form-item label="PackageUid:" :label-width="formLabelWidth">-->
                <!--<el-input v-model.trim="pUid" auto-complete="off" disabled></el-input>-->
            <!--</el-form-item>-->
            <el-form-item label="FullChineseName:" :label-width="formLabelWidth" v-if="titleShow==true">
                <el-input v-model.trim="form.FullChineseName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="Function:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Function" auto-complete="off"></el-input>
            </el-form-item>
            <!--<el-form-item label="PackageItemUid:" :label-width="formLabelWidth" v-if="titleShow==true">-->
                <!--<el-input v-model.trim="uid" auto-complete="off" disabled></el-input>-->
            <!--</el-form-item>-->
            <el-form-item label="FunctionName:" :label-width="formLabelWidth"   :required="true">
                <el-input v-model.trim="form.FunctionName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="Templatecode:" :label-width="formLabelWidth"  :required="true">
                <el-input v-model.trim="form.Templatecode" auto-complete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">Cancel</el-button>
            <el-button type="primary" @click="submit()">Submit</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
.updatePwd{
    .el-dialog__header{
        padding:30px;
        .el-dialog__headerbtn{
            width:50px;
            height:50px;
            margin:-30px -30px 0 0;
        }
    }
    .el-dialog__body{
        padding:30px 50px 0 0;
    }
}
</style>

<script>
import QuRequest from '../../../core/QuRequest'

const quRequest = new QuRequest()

export default {

    data() {
        return {
            Gender:[
                { 'name': 'man', 'value': true},
                { 'name': 'women','value': false},
            ],
            IsPartime:[
                { 'name': 'yes', 'value': true},
                { 'name': 'no','value': false},
            ],
            IsActive:[
                { 'name': 'yes', 'value': true},
                { 'name': 'no','value': false},
            ],
            MaritalStatus:[
                { 'name': 'Single','value': 0},
                { 'name': 'Married', 'value': 1},
            ],
            titleShow:false,
            dialogForm: false,
            formLabelWidth: '150px',
            disabled:true,
            isShow:true,
            uid:'',
            pUid:'',
            EmpCode:'',
            filter: {
                w: 'Employee.EmpCode EQ ' + this.$route.query.EmpCode + ',PackageItem.PackageUid EQ ' + this.$route.query.PackageUid
            },
            form:{
                'CompanyCode':'',
                'Templatecode': '',
               // 'PackageUid':'',
               // 'PackageItemUid':'',
                'Function':'',
                'FullChineseName':'',
                'EffectiveDate':'',
                'Amount':'',
            }
        }
    },
    computed:{
        singleDate: {
            set (newValue) {
                if(newValue){
                    this.form.EffectiveDate = this.formatDate(newValue)
                }else{
                    this.form.EffectiveDate = '';
                }
            },
            get () {
                return this.form.EffectiveDate
            }
        },
    },
    mounted() {
        this.getUid();
        this.getList();
    },
    methods: {
        getUid(){
            quRequest.send(quRequest.Apis.qutuiba.member.uid
            ).then(res => {
                this.uid = res.result[0];
            }).catch(err => {
                this.$message.error(`Failed to retrieve data：${ err.message || 'Unknown error' }`);
            }).fin(() => {
                this.loading = false;
            }).done();
        },
        formatDate(dateStr){
            let date = new Date(dateStr);
            let y = date.getFullYear();
            let m = date.getMonth() + 1;
            let d = date.getDate();
            return [y, this.addZero(m), this.addZero(d)].join('/');
        },
        addZero: function (num) {
            return (num > 9 ? '' : '0') + num
        },
        showInfoDialog (empcode,PackageUid,item) {
            if(arguments.length>2){
                this.isShow = false;
                this.form.Amount= item.Amount;
                this.form.CompanyCode =item.CompanyCode;
                this.form.EffectiveDate=item.EffectiveDate;
               // this.form.FullChineseName= item.FullChineseName;
                this.form.Function =item.Function;
               // this.form.PackageItemUid=item.PackageItemUid;
               // this.form.PackageUid =item.PackageUid;
                this.form.Templatecode= item.Templatecode;
                this.titleShow = false;
            }else{
                this.titleShow = true;
                this.form.Amount= '';
                this.form.EffectiveDate ='';
               // this.form.EmpCode = '';
             //   this.form.PackageItemUid='';
                this.form.Function ='';
                //this.form.PackageUid = '';
                this.form.Templatecode = '';
            }
            this.EmpCode = empcode;
            this.pUid = PackageUid;
            this.dialogForm = true;
        },
        getList(){
            quRequest.send(quRequest.Apis.qutuiba.member.fullChinese, this.filter)
                .then(res => {
                    this.form.CompanyCode = res.value[0].CompanyCode;
                    this.form.FullChineseName = res.value[0].FullChineseName;
                })
                .catch(err => {
                    this.$message.error(`Failed to retrieve data：${ err.message || 'Unknown error' }`);
                })
                .fin(() => {
                }).done();

        },
        submit(){
            quRequest.send(quRequest.Apis.qutuiba.member.update,this.titleShow==true?{'json' :'{update:{PackageItem:['+JSON.stringify(Object.assign({'EmpCode':this.EmpCode,'PackageItemUid':this.uid,'PackageUid':this.pUid},this.form))+']}}'}:{'json' :'{update:{PackageItem:['+JSON.stringify(Object.assign({'EmpCode':this.EmpCode,'PackageItemUid':this.uid,'PackageUid':this.pUid},this.form))+']}}'}
            ).then(res => {
                this.$message.success("Submit Success！");
                this.dialogForm = false;
                this.$emit('update')
            }).catch(err => {
                this.$message.error(err.message);
            }).fin(() => {
                this.loading = false;
            }).done();
        }
    }
}
</script>


